import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent
 {
   arrs = []

   newItem = "";
   pushItem()
   {
     if(this.newItem != "")
     {
       this.arrs.push(this.newItem);
       this.newItem = "";
     }
   }
   deleteItem(index)
   {
     this.arrs.splice(index, 1);
   }
 }
